* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body, html {
  font-family: 'Nunito Sans', sans-serif; }

button {
  outline: none;
  border: none;
  cursor: pointer; }

a {
  text-decoration: none;
  outline: none; }

.btn {
  font-family: 'Nunito Sans', sans-serif; }
  .btn--primary {
    font-weight: bold;
    font-size: 14px;
    background-color: #e15c2b;
    color: #ffffff;
    height: 50px;
    line-height: 50px;
    padding-left: 24px;
    padding-right: 24px;
    border-radius: 24px; }
