 * @desc manage break points media
*/
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px; }
  .header-fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99; }
  @media screen and (max-width: 767px) {
    .header {
      padding-left: 8px;
      padding-right: 8px; } }
  .header__brand__img {
    width: 162px;
    height: 64px; }
    @media screen and (min-width: 1025px) and (max-width: 1199px) {
      .header__brand__img {
        width: 140px;
        height: auto; } }
    @media screen and (min-width: 992px) and (max-width: 1024px) {
      .header__brand__img {
        width: 120px;
        height: auto; } }
    @media screen and (min-width: 768px) and (max-width: 991px) {
      .header__brand__img {
        width: 100px;
        height: auto; } }
    @media screen and (max-width: 767px) {
      .header__brand__img {
        width: 100px;
        height: auto; } }
  .header__menu {
    height: 38px; }
    .header__menu__item {
      display: inline-block;
      line-height: 38px;
      font-weight: bold;
      position: relative; }
      .header__menu__item::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 30px;
        height: 2px;
        background-color: #e15c2b;
        display: none; }
      .header__menu__item a {
        color: #1c1e40; }
      .header__menu__item + .header__menu__item {
        margin-left: 54px; }
        @media screen and (min-width: 1200px) and (max-width: 1499px) {
          .header__menu__item + .header__menu__item {
            margin-left: 30px; } }
      .header__menu__item:last-child {
        margin-left: 140px; }
        @media screen and (min-width: 1200px) and (max-width: 1499px) {
          .header__menu__item:last-child {
            margin-left: 50px; } }
      .header__menu__item--active::before {
        display: block; }
      .header__menu__item--active a {
        color: #e15c2b; }
    @media screen and (max-width: 1199px) {
      .header__menu {
        display: none;
        position: absolute;
        top: 80px;
        left: 0;
        width: 100%;
        height: calc(100% - 80px);
        padding-left: calc((100% - 950px) / 2);
        padding-top: 8px;
        background: -webkit-radial-gradient(top, circle, #e15c2b, #8934b5);
        background: radial-gradient(circle at top, #e15c2b, #8934b5);
        -webkit-animation: bground 450ms ease-in-out forwards;
                animation: bground 450ms ease-in-out forwards; }
      @-webkit-keyframes bground {
        from {
          opacity: .4; }
        to {
          opacity: 1; } }
      @keyframes bground {
        from {
          opacity: .4; }
        to {
          opacity: 1; } }
        .header__menu.active {
          display: block; }
        .header__menu__item {
          display: block;
          margin-left: 0 !important;
          -webkit-animation: run-to-right 450ms ease-in-out forwards;
                  animation: run-to-right 450ms ease-in-out forwards; }
          .header__menu__item:last-child {
            -webkit-animation-name: bottom-to-top;
                    animation-name: bottom-to-top; }
          .header__menu__item:nth-child(1) {
            -webkit-animation-duration: 450ms;
                    animation-duration: 450ms; }
          .header__menu__item:nth-child(2) {
            -webkit-animation-duration: 525ms;
                    animation-duration: 525ms; }
          .header__menu__item:nth-child(3) {
            -webkit-animation-duration: 600ms;
                    animation-duration: 600ms; }
          .header__menu__item:nth-child(4) {
            -webkit-animation-duration: 675ms;
                    animation-duration: 675ms; }
          .header__menu__item:nth-child(5) {
            -webkit-animation-duration: 750ms;
                    animation-duration: 750ms; }
          .header__menu__item:nth-child(6) {
            -webkit-animation-duration: 825ms;
                    animation-duration: 825ms; }
      @-webkit-keyframes run-to-right {
        from {
          -webkit-transform: translateX(50%);
                  transform: translateX(50%); }
        to {
          -webkit-transform: translateX(0);
                  transform: translateX(0); } }
      @keyframes run-to-right {
        from {
          -webkit-transform: translateX(50%);
                  transform: translateX(50%); }
        to {
          -webkit-transform: translateX(0);
                  transform: translateX(0); } }
      @-webkit-keyframes bottom-to-top {
        from {
          -webkit-transform: translateY(100px);
                  transform: translateY(100px); }
        to {
          -webkit-transform: translateY(0);
                  transform: translateY(0); } }
      @keyframes bottom-to-top {
        from {
          -webkit-transform: translateY(100px);
                  transform: translateY(100px); }
        to {
          -webkit-transform: translateY(0);
                  transform: translateY(0); } }
          .header__menu__item a {
            color: #ffffff; } }
    @media screen and (max-width: 991px) {
      .header__menu {
        padding-left: calc((100% - 750px) / 2);
        top: 70px;
        height: calc(100% - 70px); } }
    @media screen and (max-width: 767px) {
      .header__menu {
        padding-left: 8px;
        top: 68px;
        height: calc(100% - 68px); } }
  .header__btn-toggle {
    width: 48px;
    height: 48px;
    border: 1px solid #8934b5;
    border-radius: 4px;
    background-color: transparent;
    position: relative;
    display: none; }
    @media screen and (max-width: 1199px) {
      .header__btn-toggle {
        display: block; } }
    .header__btn-toggle.active .line--first {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      left: 10px;
      top: 10px;
      height: 2px; }
    .header__btn-toggle.active .line--second {
      opacity: 0; }
    .header__btn-toggle.active .line--third {
      bottom: 10px;
      height: 2px;
      left: 10px;
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }
    .header__btn-toggle .line {
      position: absolute;
      left: 4px;
      width: 36px;
      height: 1px;
      background-color: #8934b5;
      -webkit-transform-style: preserve-3d;
              transform-style: preserve-3d;
      -webkit-transition: all 450ms ease-in-out;
      transition: all 450ms ease-in-out; }
      .header__btn-toggle .line--first {
        top: 12px;
        -webkit-transform-origin: left;
                transform-origin: left; }
      .header__btn-toggle .line--second {
        top: 22px;
        opacity: 1; }
      .header__btn-toggle .line--third {
        -webkit-transform-origin: left;
                transform-origin: left;
        bottom: 12px; }

